___CSS_LOADER_EXPORT___.push([module.id, ".selec[data-v-36d28058]{color:#0808a7;font-weight:700}.by-preview[data-v-36d28058]{height:100%;min-width:1070px;width:100%}iframe[data-v-36d28058]{border:none;height:100%!important;scale:(.7);width:100%!important}.padre[data-v-36d28058]{overflow-x:visible;white-space:nowrap}.contenedorIframe[data-v-36d28058]{background:none}.hijo[data-v-36d28058]{transform:scale(.6)}@media (max-width:600px){.contenedorIframe[data-v-36d28058]{overflow-x:scroll}.botones[data-v-36d28058]{justify-content:space-between;margin-bottom:5px;margin-left:-5px;margin-right:-10px}.botones[data-v-36d28058],.btn[data-v-36d28058]{width:100%}.inputs[data-v-36d28058]{display:block}}", "",{"version":3,"sources":["webpack://./resources/js/Pages/Admin/Reportes/index.vue"],"names":[],"mappings":"AA8cA,wBACA,aAAA,CACA,eACA,CACA,6BAEA,WAAA,CACA,gBAAA,CAFA,UAGA,CACA,wBACA,WAAA,CAEA,qBAAA,CACA,UAAA,CAFA,oBAGA,CACA,wBACA,kBAAA,CACA,kBACA,CACA,mCACA,eACA,CACA,uBACA,mBACA,CACA,yBACA,mCACA,iBACA,CACA,0BAEA,6BAAA,CAGA,iBAAA,CAFA,gBAAA,CACA,kBAEA,CACA,gDANA,UAQA,CACA,yBACA,aACA,CAEA","sourcesContent":["<template>\r\n<v-container  style=\"background:white;\">\r\n  <div class=\"\" style=\"background:white;\">\r\n    <div class=\"text-center\">\r\n      <v-snackbar\r\n        v-model=\"snackbar\"\r\n        :timeout=\"timeout\"\r\n      >\r\n        {{ text }}\r\n\r\n        <template v-slot:action=\"{ attrs }\">\r\n          <v-btn\r\n            color=\"blue\"\r\n            text\r\n            v-bind=\"attrs\"\r\n            @click=\"snackbar = false\"\r\n          >\r\n            Close\r\n          </v-btn>\r\n        </template>\r\n      </v-snackbar>\r\n    </div>\r\n    <v-row style=\"background: white; margin-bottom:-25px;\" >\r\n        <v-col md=\"9\" lg=\"9\">\r\n            <h3>Cargo de bienes </h3>\r\n        </v-col>\r\n        <v-col md=\"3\" lg=\"3\" class=\"right\" style=\"text-align: right;\">\r\n            <div><span @click=\"opcionTodos\" :class=\"{ selec: opcion === 0 }\" style=\"cursor:pointer\" >Todos</span> | <span @click=\"opcionNoR\" :class=\"{ selec: opcion===1 }\" style=\"cursor:pointer\">No registrados</span></div>\r\n        </v-col>\r\n    </v-row>\r\n    <v-row class=\"inputs\" style=\"background: white\">\r\n        <v-col sx=\"12\" sm=\"12\" md=\"4\" lg=\"4\" style=\"margin-bottom:-40px;\" class=\" \" >\r\n            <v-autocomplete\r\n                v-model=\"areE\"\r\n                clearable\r\n                dense\r\n                label=\"Oficina\"\r\n                outlined\r\n                :items=\"oficinas\"\r\n                :filter=\"customFilter\"\r\n                item-value=\"iduoper\"\r\n                item-text=\"nombre\"\r\n                :search-input.sync=\"oficinas_search\"\r\n                required\r\n            >\r\n                <template v-slot:no-data>\r\n                    <v-list-item>\r\n                        <v-list-item-title>\r\n                            <template v-if=\"oficinas_search?.length > 0\">\r\n                                <!-- Datos no encontrados para -->\r\n                                <!-- <strong>\r\n                                    {{ oficinas_search }}\r\n                                </strong> -->\r\n                            </template>\r\n                            <template v-else>\r\n                                No hay registros en el inventario\r\n                            </template>\r\n                        </v-list-item-title>\r\n                    </v-list-item>\r\n                </template>\r\n\r\n                <template v-slot:item=\"data\">\r\n                    <v-list-item-content>\r\n                        <v-list-item-title v-html=\"data.item.codigo\">\r\n                        </v-list-item-title>\r\n                        <v-list-item-subtitle>\r\n                            {{ data.item.nombre }}\r\n                        </v-list-item-subtitle>\r\n                    </v-list-item-content>\r\n                </template>\r\n            </v-autocomplete>\r\n\r\n        </v-col>\r\n\r\n        <v-col sx=\"12\" sm=\"12\" md=\"4\" lg=\"4\" style=\"margin-bottom:-40px;\" class=\"p-0\" >\r\n            <v-autocomplete\r\n                v-model=\"perE\"\r\n                clearable\r\n                class=\"mt-0 pt-0\"\r\n                dense\r\n                label=\"Persona\"\r\n                outlined\r\n                :items=\"personas\"\r\n                :filter=\"customFilterPersona\"\r\n                item-value=\"id\"\r\n                item-text=\"nombre\"\r\n                :search-input.sync=\"personas_search\"\r\n                required\r\n            >\r\n                <template v-slot:no-data>\r\n                    <v-list-item>\r\n                        <v-list-item-title>\r\n                            <template v-if=\"oficinas_search?.length > 0\">\r\n                                Datos no encontrados para\r\n                                <strong>\r\n                                    {{ oficinas_search }}\r\n                                </strong>\r\n                            </template>\r\n                            <template v-else>\r\n                                No hay registros en el inventario\r\n                            </template>\r\n                        </v-list-item-title>\r\n                    </v-list-item>\r\n                </template>\r\n\r\n                <template v-slot:item=\"data\">\r\n                    <v-list-item-content>\r\n                        <v-list-item-title v-html=\"data.item.dni\">\r\n                        </v-list-item-title>\r\n                        <v-list-item-subtitle>\r\n                            {{ data.item.nombres }} {{ data.item.paterno }} {{ data.item.materno }}\r\n                        </v-list-item-subtitle>\r\n                    </v-list-item-content>\r\n                </template>\r\n            </v-autocomplete>\r\n        </v-col>\r\n    </v-row>\r\n    <!-- {{ registrado }} -->\r\n    <div style=\" overflow-y:hidden; width:100%; margin-top:30px; height:425px;\" class=\"contenedorIframe\" >\r\n        <div  v-if=\"areE !== null && perE !== null\"  class=\"by-preview\" style=\"transform:scale(1);\">\r\n            <iframe :src=\"preview\" scrolling=\"yes\" frameborder=\"0\" style=\" padding-top:-30px;  border:solid 0.5px #cdcdf4;\"> </iframe>\r\n        </div>\r\n    </div>\r\n    <v-col style=\"display: flex; justify-content:flex-end;\" sx=\"12\" sm=\"12\" md=\"12\" lg=\"12\" >\r\n        <div class=\"ml-2\" v-if=\"registrado === 0\">\r\n            <v-btn height=\"38\" small class=\"btn\" dark color=\"primary\" @click=\"dialogGuardar\">Imprimir</v-btn>\r\n        </div>\r\n        <div class=\"ml-2\" v-if=\"registrado === 1\">\r\n                <v-btn height=\"38\" small class=\"btn\" color=\"grey-100\" @click=\"dialogBloq\">Imprimir</v-btn>\r\n        </div>\r\n\r\n        <!-- <div class=\"ml-2\">\r\n            <v-btn height=\"38\" small class=\"btn\" dark color=\"primary\" @click=\"PrintPdf\">Print PDF</v-btn>\r\n        </div> -->\r\n        <!-- <div class=\" ml-2\" small>\r\n            <v-btn height=\"38\" class=\"btn\" dark color=\"primary\" @click=\"Guardar\" >Guardar PDF</v-btn>\r\n        </div> -->\r\n    </v-col>\r\n\r\n    <v-row justify=\"center\">\r\n        <v-dialog\r\n            v-model=\"dialog\"\r\n            persistent\r\n            max-width=\"300\"\r\n            >\r\n            <v-card>\r\n            <v-card-title class=\"text-h6 white--text primary lighten-1\">\r\n                    Advertencia!\r\n            </v-card-title>\r\n            <v-card-text>\r\n                <h4 class=\"mt-4 m-2\" style=\"text-align:center;\" >\r\n                    {{ mensaje }} <span v-if=\"docSeleccionado !== null\"> <span color=\"black\"> {{docSeleccionado.dni}} </span> de {{ docSeleccionado.nombre }} </span>\r\n                </h4>\r\n            </v-card-text>\r\n            <v-card-actions>\r\n            <v-spacer></v-spacer>\r\n            <v-btn\r\n                color=\"danger darken-1\"\r\n                outlined\r\n                text\r\n                @click=\"dialog = false\"\r\n            >\r\n                Cancelar\r\n            </v-btn>\r\n            <v-btn\r\n                color=\"primary lighten-1\"\r\n                @click=\"generarPDF\"\r\n            >\r\n                Continuar\r\n            </v-btn>\r\n            </v-card-actions>\r\n        </v-card>\r\n        </v-dialog>\r\n    </v-row>\r\n\r\n    <v-row justify=\"center\">\r\n        <v-dialog\r\n            v-model=\"dialogBloqueado\"\r\n            persistent\r\n            max-width=\"300\"\r\n            >\r\n            <v-card>\r\n            <v-card-title class=\"text-h6 white--text primary lighten-1\">\r\n                    Advertencia!\r\n            </v-card-title>\r\n            <v-card-text>\r\n                <h4 class=\"mt-4 m-2\" style=\"text-align:center;\" >\r\n                    Ya se  generó un documento para <span v-if=\"docSeleccionado !== null\"> <span color=\"black\"> {{docSeleccionado.dni}} </span> de {{ docSeleccionado.nombre }}, si desea continuar debe desbloquear el documento {{docSeleccionado.codigo }} </span>\r\n                </h4>\r\n            </v-card-text>\r\n            <v-card-actions>\r\n            <v-spacer></v-spacer>\r\n            <v-btn\r\n                color=\"danger darken-1\"\r\n                outlined\r\n                text\r\n                @click=\"dialogBloqueado = false\"\r\n            >\r\n                Cancelar\r\n            </v-btn>\r\n            <v-btn\r\n                color=\"primary lighten-1\"\r\n                disabled\r\n            >\r\n                Continuar\r\n            </v-btn>\r\n            </v-card-actions>\r\n        </v-card>\r\n        </v-dialog>\r\n    </v-row>\r\n\r\n\r\n\r\n</div>\r\n\r\n\r\n  </v-container>\r\n</template>\r\n<script>\r\nimport Layout from \"@/Layouts/AdminLayout\";\r\nimport axios from 'axios';\r\nexport default {\r\n    metaInfo: { title: \"Personas\" },\r\n    layout: Layout,\r\n    data () {\r\n      return {\r\n        url:'',\r\n        dialog: false,\r\n        preview:'',\r\n        searchdocuments:\"\",\r\n        areas: [],\r\n        oficinas: [],\r\n        personas:[],\r\n        documentos:[],\r\n        docSeleccionado: null,\r\n        areEO:null,\r\n        areE:null,\r\n        perE:null,\r\n        ofiE:null,\r\n        snackbar: false,\r\n        text: '',\r\n        PDF:null,\r\n        timeout: 2000,\r\n        oficinas_search: \"\",\r\n        areas_search: \"\",\r\n        personas_search: \"\",\r\n        data: [],\r\n        mensaje:'',\r\n        registrado:0,\r\n        opcion:1,\r\n        dialogBloqueado:false,\r\n      }\r\n    },\r\n    created() {\r\n        this.getPersonas()\r\n        this.getOficinas()\r\n        this.getDocumentos()\r\n    },\r\n\r\n    watch:{\r\n        areE: function(){\r\n            this.getPersonas();\r\n            this.Registrado(this.areE, this.perE)\r\n            this.preview = null;\r\n        },\r\n        perE: function(){\r\n            this.preview = '/admin/reportes/preview/'+this.areE+'/'+this.perE+'#toolbar=0';\r\n            this.Registrado(this.areE, this.perE)\r\n        },\r\n\r\n\r\n    },\r\n\r\n    methods: {\r\n        opcionNoR(){\r\n            this.opcion = 1;\r\n            if(this.areE === null && this.perE !== null) {\r\n                 this.areE === null;\r\n                 this.getOficinas();\r\n            }else {\r\n                if(this. areE !== null && this.perE === null ) {\r\n                    this.perE = null;\r\n                    this.getPersonas();\r\n                }\r\n                else {\r\n                    if(this.areE !== null && this.perE !== null) {\r\n                        this.perE = null;\r\n                        this.areE = null;\r\n                        this.getPersonas();\r\n                    }\r\n\r\n                }\r\n            }\r\n        },\r\n        opcionTodos(){\r\n            this.opcion = 0;\r\n            if(this.areE === null && this.perE !== null) {\r\n                this.getAreas();\r\n                this.areE === null;\r\n            }else {\r\n                if(this. areE !== null && this.perE === null ) {\r\n                    this.perE = null;\r\n                    this.getPersonas();\r\n                }\r\n                else {\r\n                    if(this.areE !== null && this.perE !== null) {\r\n                        this.perE = null;\r\n                        this.areE = null;\r\n                        this.getPersonas();\r\n                        this.getAreas();\r\n                    }\r\n\r\n                }\r\n            }\r\n        },\r\n        customFilter(item, queryText, itemText) {\r\n            const nombre = item.nombre.toLowerCase();\r\n            const codigo = item.codigo.toLowerCase();\r\n            const searchText = queryText.toLowerCase();\r\n            return (\r\n                nombre.indexOf(searchText) > -1 ||\r\n                codigo.indexOf(searchText) > -1\r\n             );\r\n        },\r\n\r\n        customFilterAreas(item, queryText, itemText) {\r\n            const nombre = item.nombre.toLowerCase();\r\n            const codigo = item.codigo.toLowerCase();\r\n            const searchText = queryText.toLowerCase();\r\n            return (\r\n                nombre.indexOf(searchText) > -1 ||\r\n                codigo.indexOf(searchText) > -1\r\n             );\r\n        },\r\n        customFilterPersona(item, queryText, itemText) {\r\n            const nombres = item.nombres.toLowerCase();\r\n            const dni = item.dni.toLowerCase();\r\n            const searchText = queryText.toLowerCase();\r\n            return (\r\n                nombres.indexOf(searchText) > -1 ||\r\n                dni.indexOf(searchText) > -1\r\n             );\r\n        },\r\n\r\n        async getOficinas() {\r\n            let res = await axios.get(\"/admin/oficinas/getallOficinas\");\r\n            console.log(res.data);\r\n            this.oficinas = res.data.datos;\r\n            return res.data.datos.data;\r\n        },\r\n\r\n        async getDocumentos() {\r\n            let res = await axios.get(\"/admin/reportes/getDocuments\");\r\n            this.documentos = res.data.datos;\r\n            return res.data.datos.data;\r\n        },\r\n\r\n        async getPersonas() {\r\n            if (this.areE === null ){\r\n                let res = await axios.get(\"/admin/personas/getPersonasInv\");\r\n                console.log(res.data);\r\n                this.personas = res.data.datos;\r\n                return res.data.datos.data;\r\n            }\r\n            else {\r\n                if( this.areE !== null ){\r\n                    if (this.opcion === 0){\r\n                        let res = await axios.get(\"/admin/personas/getPersonasByAreaInv/\"+this.areE);\r\n                        console.log(res.data);\r\n                        this.personas = res.data.datos;\r\n                        return res.data.datos.data;\r\n                    } else {\r\n                        if (this.opcion === 1) {\r\n                            let res = await axios.get(\"/admin/personas/getPersonasByAreaInvNoR/\"+this.areE);\r\n                            console.log(res.data);\r\n                            this.personas = res.data.datos;\r\n                            return res.data.datos.data;\r\n                        }\r\n                    }\r\n                }\r\n            }\r\n        },\r\n\r\n        buscabyOficinaID(id){\r\n            console.log(id)\r\n            for(let i in this.oficinas){\r\n                if (this.oficinas[i] === id ){\r\n                    this.ofiE = this.oficinas[i].id\r\n                    this.oficinaElegida = this.oficinas[i].nombre\r\n                }\r\n            }\r\n        },\r\n        buscaAreabyID(id){\r\n            for(let i in this.areas2){\r\n                if (this.areas2[i].id === id ){\r\n                    return this.areas2[i].nombre\r\n                }\r\n            }\r\n        },\r\n        Registrado(idA, idP){\r\n            this.registrado = 0;\r\n            for(let i in this.documentos){\r\n                if (this.documentos[i].id_area === idA && this.documentos[i].id_persona === idP ){\r\n                    if(this.documentos[i].estado ===  0 ){\r\n                        this.registrado = 1;\r\n                    }\r\n                    this.docSeleccionado = this.documentos[i];\r\n                }\r\n            }\r\n        },\r\n        dialogBloq(){\r\n            this.dialogBloqueado = true;\r\n        },\r\n        dialogGuardar(){\r\n            this.mensaje = \"Se bloquearan todos los bienes de este documento y los inventariadores no podrán registrar más bienes para\";\r\n            this.dialog = true;\r\n        },\r\n        dialogImprimir(){\r\n            this.mensaje = \"Se bloquearan todos los bienes de este documento\";\r\n            this.dialog = true;\r\n        },\r\n        generarPDF(){\r\n            this.dialog = false;\r\n            let res = axios.get(\"/admin/pdfBienes/\"+this.perE+\"/\"+this.areE)\r\n            .then(response => {\r\n                 console.log(response);\r\n                 this.PDF=response.data.datos;\r\n//                 this.url = this.PDF.url+\"#toolbar=0\";\r\n                   this.PrintPdf(this.PDF.url);\r\n                   this.url = this.PDF.url;\r\n             });\r\n            this.text = \"Documento Guardado\"\r\n            this.snackbar = true\r\n            this.getDocumentos()\r\n            this.areE = null;\r\n            this.ofiE = null;\r\n            this.perE = null;\r\n            },\r\n\r\n        async Guardar() {\r\n            this.PDF['id_persona'] = this.perE;\r\n            let res = await axios.post(\r\n                \"/admin/documentos/guardar\",\r\n                this.PDF\r\n            );\r\n\r\n\r\n        },\r\n\r\n        PrintPdf (URL) {\r\n            var iframe = document.createElement('iframe');\r\n            iframe.style.display = \"none\";\r\n            iframe.src = URL;\r\n            document.body.appendChild(iframe);\r\n            iframe.contentWindow.focus();\r\n            iframe.contentWindow.print();\r\n        }\r\n\r\n    },\r\n  }\r\n</script>\r\n<style scoped>\r\n.selec{\r\n    color: #0808a7;\r\n    font-weight: bold;\r\n}\r\n.by-preview{\r\n    width:100%;\r\n    height:100%;\r\n    min-width:1070px;\r\n}\r\niframe{\r\nborder:none;\r\nwidth: 100% !important;\r\n  height: 100% !important;\r\n  scale:(0.7);\r\n}\r\n.padre{\r\n   overflow-x: visible;\r\n   white-space: nowrap;\r\n  }\r\n.contenedorIframe{\r\n    background: none;\r\n}\r\n.hijo{\r\n    transform: scale(0.6)\r\n}\r\n@media (max-width: 600px) {\r\n    .contenedorIframe{\r\n        overflow-x:scroll;\r\n    }\r\n  .botones {\r\n    width: 100%;\r\n    justify-content: space-between;\r\n    margin-left: -5px;\r\n    margin-right: -10px;\r\n    margin-bottom: 5px;\r\n  }\r\n  .btn{\r\n    width: 100%;\r\n  }\r\n    .inputs{\r\n        display: block;\r\n    }\r\n\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
